  --white: #fffbfb;
  --grey: #f0eef1;
  --black: #050505;
  --btn-hover: #a1a1aa;
  --close-btn: #fecaca;
  --submit-btn: #d9f99d;
  --close-btn-hover: #f87171;
  --submit-btn-hover: #a3e635;
  --box-shadow: rgba(0, 0, 0, 0.16) 0px 2px 4px;
  --read-btn: #fde047;
  --remove-btn: #f87171;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  outline: none;
  box-sizing: border-box;
  -webkit-appearance: none;
  appearance: none;
}

body {
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-areas:
    "header"
    "main"
    "footer";
  font-size: 14px;
  font-family: "Poppins";
}
/*========================HEADER======================*/
header {
  grid-area: header;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.2rem 14rem;
  background-color: var(--white);
  box-shadow: rgba(0, 0, 0, 0.16) 0px 2px 4px;
  position: relative;
}
header h1 {
  font-weight: 700;
  font-size: 2rem;
}
header > .theme {
  display: flex;
  gap: 5rem;
  align-items: center;
}
.theme > button {
  width: 6rem;
  border: 0;
  border-radius: 20px;
  font-family: "Poppins";
  font-size: 1.3rem;
  padding: 1rem;
  cursor: pointer;
  transition: all 0.5s ease;
}

/*========================MAIN======================*/

main {
  grid-area: main;
  background-color: var(--grey);
  width: 100%;
  padding: 1.2rem 14rem;
}
main > div {
  text-align: center;
}
.add-book-btn {
  width: 11rem;
  border: 0;
  border-radius: 20px;
  font-family: "Poppins";
  font-size: 1.5rem;
  font-weight: 600;
  padding: 1rem 10px;
  cursor: pointer;
  transition: all 0.5s ease;
}
.add-book-btn:hover,
.btn:hover {
  background-color: var(--btn-hover);
}
/*========================MODAL======================*/

dialog {
  border: 0;
  border-radius: 12px;
  padding: 2.3rem 1rem;
  font-family: "Poppins";

  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
dialog::backdrop {
  background-color: rgba(0, 0, 0, 0.8);
}

form {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  font-size: 1.2rem;
}
form button {
  padding: 0.8rem 1.7rem;
  border-radius: 12px;
  border: 0;
  background-color: var(--submit-btn);
  cursor: pointer;
  transition: all 0.5s ease;
  font-weight: 500;
  font-family: inherit;
}
.buttons {
  display: flex;
  justify-content: space-evenly;
}
.close-btn {
  background-color: var(--close-btn);
}
.submit-btn:hover {
  background-color: var(--submit-btn-hover);
}
.close-btn:hover {
  background-color: var(--close-btn-hover);
}
input[type="text"] {
  padding: 20px;
  border: 2px solid var(--grey);
  border-radius: 12px;
  width: 300px;
  font-size: 18px;
}
label {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-weight: 500;
  height: 100%;
  font-size: 16px;
}
input[type="checkbox"] {
  width: 20px;
  height: 20px;
  border: 2px solid var(--grey);
  cursor: pointer;
  font: inherit;
  transition: all 0.5s ease;
  appearance: checkbox;
}
input[type="checkbox"]:checked {
  color: #bef264;
}
input[type="checkbox"]:hover {
  border: 2px solid var(--black);
}
/*========================FOOTER======================*/
article {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 40px;
  text-align: center;
  grid-template-rows: auto;
  font-family: inherit;
}
article div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 20px;
  gap: 20px;
  padding: 20px;
  border-radius: 12px;
  background-color: var(--white);
  box-shadow: var(--box-shadow);
  line-height: 1.2;
  font-weight: 500;
  position: relative;
  z-index: 1;
}
article div button {
  border: 0;
  border-radius: 12px;
  padding: 1rem;
  transition: all 0.3s ease;
  cursor: pointer;
  font-size: 20px;
  font-weight: 500;
}
.read-btn {
  background-color: var(--read-btn);
}
.read-btn:hover {
  background-color: var(--submit-btn-hover);
}
.remove-btn {
  background-color: var(--close-btn-hover);
}
.read-btn.isRead {
  background-color: var(--submit-btn-hover);
  border: 0;
  transition: all 0.2s ease;
}

/*========================FOOTER======================*/

footer {
  grid-area: footer;
  background-color: var(--grey);
  padding: 0.8rem;
  text-align: center;
  font-family: "Poppins";
  font-weight: 500;
  font-size: 18px;
}
footer a {
  color: var(--black);
  transition: all 1s ease;
}
footer a:hover {
  text-decoration: none;
}
footer a:visited {
  color: var(--black);
}
footer a:active {
  color: var(--black);
}
